<template>
  <div id="onlineService">
    <div v-if="concern_data.length>0" class="myConcern">
      <my-title :title="'我的关注'" v-show="data_one.length>0" :more_show="false"></my-title>
      <my-service-p v-for="(item,index) in data_one" :key=index :data="item"
                    @clickC="cancelConcern(index)"></my-service-p>
    </div>
    <div class="serviceList">
      <my-title :title="'客服列表'" :more_show="false"></my-title>
      <my-service-p v-for="(item,index) in data_two" :key=index :data="item"
                    @clickC="concern(index)"></my-service-p>
    </div>
  </div>
</template>

<script>
import myTitle from '../../../components/myTitle'
import myServiceP from '../../../components/myServiceP'

import myUtil from '../../../common/myUtil'

export default {
  name: 'onlineService',
  components: {
    myTitle,
    myServiceP
  },
  data () {
    return {
      concern_data: [
        {
          name: '服务人员',
          online: true,
          id: '112',
          reputably: '12',
          concerned: true
        }
      ],
      data_one: [],
      data_two: [],
      service_data: [
        {
          name: '服务人员',
          online: true,
          id: '112',
          reputably: '11',
          concerned: false
        }, {
          name: '服务人员',
          online: true,
          id: '112',
          reputably: '12',
          concerned: false
        }, {
          name: '服务人员',
          online: true,
          id: '112',
          reputably: '13',
          concerned: false
        }, {
          name: '服务人员',
          online: true,
          id: '112',
          reputably: '4',
          concerned: false
        }, {
          name: '服务人员',
          online: true,
          id: '112',
          reputably: '17',
          concerned: false
        }, {
          name: '服务人员',
          online: true,
          id: '112',
          reputably: '3',
          concerned: false
        }, {
          name: '服务人员',
          online: false,
          id: '112',
          reputably: '2',
          concerned: false
        }, {
          name: '服务人员',
          online: true,
          id: '112',
          reputably: '1',
          concerned: false
        }, {
          name: '服务人员',
          online: true,
          id: '112',
          reputably: '1',
          concerned: false
        }, {
          name: '服务人员',
          online: true,
          id: '112',
          reputably: '22',
          concerned: false
        }, {
          name: '服务人员',
          online: true,
          id: '112',
          reputably: '33',
          concerned: false
        }
      ]
    }
  },
  mounted () {
    this.$store.commit('setCurrentTitle', '在线客服')
    // setTimeout(() => {
    this.query()
    // }, 3000)
  },
  methods: {
    query () {
      // this.concern_data = []
      this.data_one = this.sortOnline(myUtil.sortObject(this.concern_data, 'reputably', true))
      this.data_two = this.sortOnline(myUtil.sortObject(this.service_data, 'reputably', true))
    },
    advisorUserList () {
      this.$http.advisorUserList({}).then((res) => {})
    },
    sortOnline (val) {
      let l = val.length
      for (let i = 0; i < l; i++) {
        if (val[i].online === false) {
          let temp = val.splice(i, 1)
          val.push(temp[0])
          l--
        }
      }
      return val
    },
    cancelConcern (index) {
      this.data_one[index].concerned = false
      this.data_two.push(this.data_one[index])
      this.data_one.splice(index, 1)
      this.concern_data = this.data_one
      this.service_data = this.data_two
      this.query()
      this.unsubscribeAdvisor()
    },
    concern (index) {
      this.data_two[index].concerned = true
      this.data_one.push(this.data_two[index])
      this.data_two.splice(index, 1)
      this.concern_data = this.data_one
      this.service_data = this.data_two
      this.query()
      this.subscribeAdvisor()
    },
    // 关注客服人员
    subscribeAdvisor () {
      this.$http.subscribeAdvisor({
        advisorUserId: 1212
      }).then((res) => {})
    },
    // 取消关注客服人员
    unsubscribeAdvisor () {
      this.$http.unsubscribeAdvisor({
        advisorUserId: 1212
      }).then((res) => {})
    }
  }
}
</script>

<style scoped>
  #onlineService {
    position: fixed;
    top: 3rem;
    width: 100%;
    bottom: 0;
    overflow-y: auto;
  }
</style>



